<template>
  <a class="mdl-button mdl-button--colored mdl-button--raised" v-on:click="restart">
    Restart
  </a>
</template>

<script>
export default {
  name: 'restart-button',
  methods: {
    restart: function () {
      this.$emit('restart');
    }
  }
}
</script>

<style>
  .material-icons.github{
    display: block;
    background: url('data:image/png;base64,');
    height: 25px;
    width: 25px;
    background-repeat: no-repeat;
    background-position: center center;
  }
</style>



