<template>
  <button class="mdl-button mdl-button--icon mdl-color-text--white">
    <a :href="href">
      <i class="material-icons github"></i>
    </a>
  </button>
</template>

<script>
export default {
  name: 'github-button',
  data () {
    return {
      href: 'https://github.com/ddum/barley-break-vuejs'
    }
  }
}
</script>

<style>
  .material-icons.github{
    display: block;
    background: url('data:image/png;base64,');
    height: 25px;
    width: 25px;
    background-repeat: no-repeat;
    background-position: center center;
  }
</style>



